import React, { Component } from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";

export default class WriteNotePanel extends Component {
    constructor(props) {
        super(props);

        this.state = {
            title: "",
            text: ""
        }
    }

    render() {
        return <div id="addNote">
            <h1> {this.props.editing ? "Edit Note" : "Create Note"} </h1>
            <Form>
                <Form.Group>
                    <Form.Label>Title</Form.Label>
                    <Form.Control
                        type="textarea"
                        placeholder="Title"
                        onChange={this.props.setActiveTitle}
                        value={this.props.activeTitle} />
                </Form.Group>
                <Form.Group>
                    <Form.Label>Text</Form.Label>
                    <Form.Control
                        as="textarea"
                        rows="8"
                        placeholder="Text"
                        onChange={this.props.setActiveText}
                        id="noteText"
                        value={this.props.activeText} />
                </Form.Group>

                <Button
                    variant={this.props.editing ? "success" : "primary"}
                    onClick={this.props.editing ? (() => this.props.editNote()) : (() => this.props.addNote())}>
                    {this.props.editing ? "Edit Note" : "Create Note"}
                </Button>

            </Form>
        </div>
    }
}